            //         store.commit('IS_ERROR', false);
            //         this.$refs['my-modal'].hide()
            //     }
            //     recognition.onerror = function () {
            //      store.commit('IS_ERROR', true);
            //       //alert('Speech recognition error detected: ' + event.error);
            //     }
            //    recognition.start();

            recognition.onresult = (event) => {
                alert("on result")
                this.speechToText = event.results[0][0].transcript;
                alert(this.speechToText)
                let payload = 'http://localhost:8080/api/v1/products?q=' + this.speechToText + "&vs=true";
                store.dispatch('GET_PRODUCT', payload)
                store.commit('IS_ERROR', false);
                this.$refs['my-modal'].hide()
            }
            recognition.onend = () => {
                if (this.speechToText == "") {
                    //this.$refs['my-modal'].show();
                    //recognition.stop();
                    //recognition.start();
                }
               // this.$refs['my-modal'].hide()
            }
            recognition.onerror = () => {
                store.commit('IS_ERROR', true);
            }
            recognition.start();
        },

        productsearch: function () {
            let payload = 'http://localhost:8080/api/v1/products?q=' + this.searchtext;
            store.dispatch('GET_PRODUCT', payload)
            //alert('h');

        },
        greet() {
            store.commit('IS_CLEAR_ALL', false);
            this.selectedbrand = [];
            this.selectedcolor = [];
            this.selectedsize = [];
            // alert('clear all');
        },

        dropselect: function () {
            let lturl = 'http://localhost:8080/api/v1/products?sortItem=' + this.selectedsort;
            // alert(this.selectedbrand.length);
            //alert(lturl);

            axios.get(lturl)
                .then(function (response) {
                    store.dispatch('SET_BRAND', {
                        data: response.data
                    })
                }).catch(function (error) {
                    // handle error
                    alert(error);
                })
        },

        commoncheck: function () {
            //alert()
            let lturl = 'http://localhost:8080/api/v1/products?sortItem=' + this.selectedsort;
            if (this.selectedbrand.length != 0 || this.selectedsize.length != 0 || this.selectedcolor.length != 0) {
                store.commit('IS_CLEAR_ALL', true);
            } else {
                // this.selectedbrand = [];
                store.commit('IS_CLEAR_ALL', false);
            }
            if (this.selectedbrand.length > 0) {
                for (var j = 0; j < this.selectedbrand.length; j++) {

                    // if (j == 0) {
                    //     lturl = lturl + '?brand=' + this.selectedbrand[j]
                    // } else {
                    //     lturl = lturl + '&brand=' + this.selectedbrand[j]
                    // }
                    lturl = lturl + '&brand=' + this.selectedbrand[j]
                }
            }

            if (this.selectedsize.length > 0) {
                for (var i = 0; i < this.selectedsize.length; i++) {

                    lturl = lturl + '&size=' + this.selectedsize[i]
                }
            }

            if (this.selectedcolor.length > 0) {
                for (var k = 0; k < this.selectedcolor.length; k++) {

                    lturl = lturl + '&color=' + this.selectedcolor[k]
                }
            }
            axios.get(lturl)
                .then(function (response) {
                    store.dispatch('SET_BRAND', {
                        data: response.data
                    })
                }).catch(function (error) {
                    // handle error
                    alert(error);
                })
        }
    },
